$links-color: #2f80ed;

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Oxygen', sans-serif;
  letter-spacing: 0.03em;
}

.root {
  height: 100%;
  width: 100%;
  display: flex;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.input-error {
  color: #f00;
  font-size: 10px;
  font-weight: bold;
  margin-top: -5px;
  margin-bottom: 10px;
}

.auth-panel {
  width: 340px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.14);
  border-radius: 12px;
  align-self: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.auth-panel_login {
  height: 450px;
}

.auth-panel_signup {
  min-height: 615px;
}

.auth-panel__header {
  font-size: 20px;
  margin: 40px 0 40px 0;
  font-weight: 700;
  text-align: center;
}

.auth-panel__link {
  display: block;
  margin-bottom: 30px;
  font-size: 13px;
  color: $links-color;
  text-decoration: none;
  text-align: center;
}

.auth-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  margin: 0 30px;
}

.auth-form__label {
  font-size: 9px;
  color: #999;
  margin-bottom: 8px;
}

.auth-form__input {
  margin-bottom: 8px;
  padding-bottom: 7px;
  border: none;
  border-bottom: 1px solid $links-color;
  font-size: 13px;
  color: #1e1e1e;

  &:focus {
    outline: none;
  }

  &::placeholder {
    font-family: 'Oxygen', sans-serif;
    font-size: 13px;
  }
}

.auth-form__button {
  height: 37px;
  background: $links-color;
  border: none;
  border-radius: 8px;
  color: #fff;
  font-family: 'Oxygen', sans-serif;
  cursor: pointer;
  margin-top: auto;
  margin-bottom: 14px;
  width: 280px;
}

.auth-form__button_center {
  align-self: center;
}

.error-page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error-page__header {
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 30px;
}

.error-page__message {
  font-size: 24px;
  line-height: 30px;
  margin: 0 0 30px 0;
}

.error-page__link {
  font-size: 24px;
  line-height: 30px;
  color: $links-color;
  text-decoration: none;
}

.error-page__nav {
  list-style: none;

  li {
    margin-left: 10px;
  }
}

.user-profile {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.user-profile__wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.avatar-wrapper {
  text-align: center;
  width: 130px;
  height: 130px;
  background: #efefef;
  border-radius: 50%;
  margin: auto;
}

.avatar-icon {
  background: url('../static/img/union.svg');
  background-position: center;
  background-size: cover;
  height: 40px;
  width: 40px;
  display: block;
  position: relative;
  top: 45px;
  left: 45px;
}

.user-profile__header {
  text-align: center;
  font-size: 24px;
  margin-top: 15px;
  margin-bottom: 50px;
}

.user-profile__header-edit {
  text-align: center;
  font-size: 24px;
  margin-top: 130px;
  margin-bottom: 82px;
}

.user-profile__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 510px;
}

.user-profile-form__label {
  font-size: 13px;
  color: #1e1e1e;
  padding-bottom: 7px;
  cursor: pointer;
}

.user-profile-form__input {
  padding-bottom: 7px;
  border: none;
  font-size: 13px;
  color: #1e1e1e;
  text-align: right;
  width: 80%;

  &:focus {
    outline: none;
  }

  &::placeholder {
    font-family: 'Oxygen', sans-serif;
    font-size: 13px;
  }

  &:disabled {
    background: none;
  }
}

.user-profile__row {
  border-bottom: 1px solid #eaeaea;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  align-items: flex-end;
}

.user-profile__links {
  margin-top: 43px;
  display: flex;
  flex-direction: column;
}

.user-profile__link {
  display: block;
  margin-bottom: 10px;
  font-size: 13px;
  color: $links-color;
  text-decoration: none;
  text-align: start;
  border-bottom: 1px solid #eaeaea;
  padding-bottom: 7px;
}

.user-profile__link_reject {
  color: #f00;
}

.user-profile__edit {
  display: flex;
}

.user-profile__aside {
  margin-left: 160px;

  .avatar-wrapper {
    margin-bottom: 14px;
  }

  .auth-panel__link {
    margin-bottom: 12px;
  }
}
